import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Base64ToGallery } from '@ionic-native/base64-to-gallery';
import { MeetingService } from '../../services/meeting';

/**
 * Generated class for the CreatePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-create',
  templateUrl: 'create.html',
})
export class CreatePage {
  private meetingDetailsFG: FormGroup;
  name: String;
  location: String;
  date: String;
  time: String;

  constructor(public navCtrl: NavController, public navParams: NavParams, public meetingService: MeetingService,
    private formBuilder: FormBuilder, private base64ToGallery: Base64ToGallery) {

    this.meetingDetailsFG = this.formBuilder.group({
      name: ['', Validators.required],
      location: ['', Validators.required],
      date: ['', Validators.required],
      time: ['', Validators.required]
    })

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreatePage');
  }

  createMeeting(): void {
    //alert(this.meetingDetailsFG.value);
    console.log(this.meetingDetailsFG.value);
    this.meetingService.create(this.meetingDetailsFG.value);

    const base64Data = ''

    this.base64ToGallery.base64ToGallery(base64Data, { prefix: '_img', mediaScanner: true }).then(
      res => console.log('Saved image to gallery ', res),
      err => console.log('Error saving image to gallery ', err)
    );



  }

}



